.search-results-wrapper {
    text-align: center;
    height: 94vh;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 1rem;
}

.search-result-group{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px 0px;
    margin-bottom: 1rem;
    width: 100%;
}

.search-result-user{
    font-size: 1.5rem;
    text-decoration: none;
    justify-self: flex-start;
    width: 100%;
}

.search-result-user-link{
    text-decoration: none;
    width: 100%;
    color: black;
    font-weight: bold;
}


.user-result-profile-image{

    max-width: 4rem;
    height: 4rem;
    object-fit: cover;
    margin: auto; /* center the image vertically */
    border-radius: 50%;
}

.search-results-title{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.25rem;
    padding-bottom: 0.5rem;
}